/* =============== GENERAL RULES =========================================================*/

* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    color: #2A2A2A;
}

body {
    background-color: #eeeeee
}

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

/* =============== NAVBAR =========================================================*/

header {
    background-color: #050000;
    box-shadow: 0 1px 10px -2px #000000b0;
    height: 160px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}

nav ul {
    list-style: none;
    text-align: center;
}

#nav-logo {
    margin: 10px;
}

#nav-logo a img {
    height: 75px;
}

nav ul li .nav-links li {
    display: inline;
    padding-right: 10px;
    padding-left: 10px;
}

nav ul li .nav-links li a {
    display: inline;
    color: rgb(249 37 143);
    text-decoration: none;
    font-weight: 600;
    font-size: 1.2rem;
}

nav ul li .nav-links li:hover a {
    color: #e5ecf4;
    font-weight: 600;
}

#active {
    font-weight: 900;
    border-bottom: 1px;
    border-bottom-style: dashed;
    padding-bottom: 2px;
}

/* =============== WALLPAPER =========================================================*/

#wallpaper {
    margin-top: 160px;
    padding: 120px;
    background-image: url(img/wallpaper.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

#wallpaper h1 {
    color: white;
    font-weight: 900;
    padding: 0px;
    margin: auto;
    text-transform: uppercase;
}

/* =============== INTRO =========================================================*/

#intro {
    margin-top: 80px;
    margin-bottom: 40px;
}

#intro p {
    font-size: 1.8rem;
}

#arrow-gif {
    filter: invert(25%) sepia(70%) saturate(3386%) hue-rotate(314deg) brightness(100%) contrast(96%);
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.presentation {
    display: grid;
    row-gap: 20px;
    column-gap: 20px;
}

.presentation a figure img {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

figure {
    display: inline-block;
    text-align: center;
}

figure img {
    vertical-align: top;
    border-top-left-radius: 40px 40px;
    border-top-right-radius: 40px 40px;
}

figure figcaption {
    padding-top: 10px;
    padding-bottom: 10px;
    text-transform: uppercase;
    font-weight: 900;
    border-bottom-left-radius: 40px 40px;
    border-bottom-right-radius: 40px 40px;
    border-top: 1px solid yellow;
    color: rgb(255, 255, 255);
    background-color: rgb(31, 31, 31);
}

/* =============== FOOTER =========================================================*/

footer {
    background-color: #2A2A2A;
    color: #FFFFFF;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

footer .container ul li {
    color: #FFFFFF;
    list-style: none;
    text-decoration: none;
}

footer .container ul li {
    padding: 10px;
}

/* =============== MEDIA QUERIES =========================================================*/

@media (min-width: 469px) {
    header {
        height: 140px;
    }

    #wallpaper {
        margin-top: 140px;
        padding: 120px;
    }
}

@media (min-width: 576px) {
    .container {
        max-width: 540px;
    }

    #wallpaper h1 {
        font-size: 2.8rem;
    }

    .presentation {
        grid-template-columns: 1fr 1fr;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 820px;
    }

    header {
        height: 75px;
    }

    #nav-logo {
        float: left;
        margin: 0;
    }

    .nav-links {
        float: right;
        margin-top: 30px;
    }

    #wallpaper {
        margin-top: 75px;
        padding: 220px;
    }

    #wallpaper h1 {
        font-size: 3.8rem;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }

    #wallpaper h1 {
        font-size: 4.2rem;
    }

    .presentation {
        grid-template-columns: 1fr 1fr 1fr;
    }

    #copyrigth li,  #copyrigth li ul{
        display: inline;
    }

}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }

    #wallpaper h1 {
        font-size: 4.8rem;
    }
}